.shopItem {
  display: flex;
  padding: 10px;
}

.shopItem__pic {
  border-radius: 2px;
  width: 66px;
  height: 66px;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  flex-shrink: 0;
  position: relative;
  box-sizing: border-box;
}

.shopItem__content {
  flex: 1 1;
}

.shopItem__title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 16px;
  font-weight: 400;
  color: #323232;
  margin-right: 3px;
}

.shopItem__comment {
  font-size: 0;
  margin: 8px 0;
  position: relative;
}

.shopItem__star {
  display: inline-block;
  width: 66px;
  height: 12px;
  vertical-align: middle;
  background: url("data:image/png;base64,")
    no-repeat;
  background-size: 86px auto;
}

.shopItem__star--50 {
  background-position: 0px -15px;
}

.shopItem__star--45 {
  background-position: 0px -32px;
}

.shopItem__star--40 {
  background-position: 0px -49px;
}

.shopItem__star--35 {
  background-position: 0px -66px;
}

.shopItem__star--30 {
  background-position: 0px -83px;
}

.shopItem__star--20 {
  background-position: 0px -100px;
}

.shopItem__star--10 {
  background-position: 0px -117px;
}

.shopItem__quantity {
  font-size: 12px;
  color: #333;
  line-height: 12px;
  margin-left: 12px;
  vertical-align: bottom;
}

.shopItem__price {
  display: inline-block;
  font-size: 12px;
  color: #333;
  line-height: 12px;
  margin-left: 12px;
  vertical-align: bottom;
}

.shopItem__price::before {
  content: "\A5";
  font-size: 12px;
  line-height:12px;
  margin-right: 2px;
  vertical-align: bottom;
  display: inline;
}

.shopItem__info {
  font-size: 12px;
  color: #999;
  line-height: 12px;
}

.shopItem__region {
  margin-right: 10px;
}

.shopList {
  background-color: #fff;
}

.shopList__filter {
  padding: 10px 0;
  font-size: 0;
}

.shopList__filterItem {
  display: inline-block;
  width: 33%;
  box-sizing: border-box;
  text-align: center;
  color: #646464;
  font-size: 13px;
  overflow: hidden;
  height: 20px;
  line-height: 20px;
  position: relative;
}

.shopList__filterItem::after {
  position: absolute;
  right: 15px;
  margin-top: 3px;
  border: 2px solid #bbb;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  width: 6px;
  height: 6px;
  border-bottom: none;
  border-left: none;
  content: "";
}

.shopList__divider {
  height: 0;
  border-top: 1px solid #e1e1e1;
}


.banner {
    display: flex;
    flex-direction: column;
    height: 120px;
    background-image: url('https://www.dpfile.com/app/app-m-module/static/666419d6641cf0faec444fc12b50383d.jpg');
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
  }
  
  .banner__title {
    display: flex;
    justify-content: center;
    margin: 24px 0 14px;
    height: 30px;
  }
  
  .banner__logo {
    height: 30px;
    width: 30px;
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: 8px;
  }
  
  .banner__text {
    font-size: 20px;
    color: #111;
    font-weight: bolder;
  }
  
  .banner__btns {
    display: flex;
    justify-content: center;
  }
  
  .banner__btn {
    height: 40px;
    width: 126px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    border-radius: 2px;
    border: 1px solid #f63;
    color: #f63;
    margin-right: 15px;
  }
  
  .banner__btn--bg {
    color: #fff;
    background-color: #f63; 
  }
.searchHeader {
  height: 45px;
  line-height: 45px;
  padding: 0 10px;
  text-align: center;
  color: #333;
  position: relative;
  z-index: 300;
  box-sizing: border-box;
  background-color: #f0f0f0;
}

.searchHeader__back {
  position: relative;
}

.searchHeader__back::before {
  border: 2px solid #f63;
  position: absolute;
  left: 4px;
  top: 22px;
  margin-top: -8px;
  display: block;
  content: "";
  width: 12px;
  height: 12px;
  border-top: none;
  border-right: none;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.searchHeader__list {
  position: absolute;
  width: 150px;
  height: 30px;
  left: 50%;
  top: 50%;
  margin-left: -75px;
  margin-top: -15px;
  font-size: 14px;
  display: flex;
}

.searchHeader__item {
  color: #ff8400;
  width: 50%;
  height: 100%;
  float: left;
  line-height: 28px;
  box-sizing: border-box;
  border: 1px solid #ff8400;
}

.searchHeader__item:first-child {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}

.searchHeader__item:last-child {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}

.searchHeader__item--selected {
  background-color: #ff8400;
  color: #fff;
}

.searchHeader__icon {
  display: block;
  border-radius: 8px;
  margin: 10px 15px 9px 8px;
  line-height: 30px;
  color: #666;
  text-align: left;
  border: 2px solid #f63;
  width: 12px;
  height: 12px;
  top: 1px;
  right: 8px;
  position: absolute;
}

.searchHeader__icon::after {
  content: "";
  display: block;
  width: 9px;
  height: 2px;
  border-top: 2px solid #f63;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  position: absolute;
  right: -6px;
  bottom: -6px;
}

.keywordBox {
  background-color: #f0f0f0;
  padding: 2px 10px 7px;
}

.keywordBox__text {
  width: 100%;
  height: 34px;
  display: block;
  color: #646464;
  line-height: 34px;
  padding-left: 35px;
  box-sizing: border-box;
  border-radius: 25px;
  position: relative;
  background-color: #fff;
}

.keywordBox__text::before {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  border: 1px solid #888;
  border-radius: 11px;
  position: absolute;
  left: 14px;
  bottom: 14px;
}

.keywordBox__text::after {
  content: "";
  display: block;
  border-top: 1px solid #888;
  border-radius: 9px;
  position: absolute;
  left: 21px;
  bottom: 13px;
  width: 6px;
  height: 0;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

